import React from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Layout from './Pages/Layout'
import Home from './Pages/Home'
import AboutMe from './Pages/AboutMe'
// import Projects from './Pages/Projects'
import Training from './Pages/Training'
import Techs from './Pages/Techs'
import Error404 from './Pages/Error404'
import TechDetail from './Pages/Techs/TechDetail'
import CertsPlatzi from './Pages/Training/CertsPlatzi'
import CertDetail from './Pages/Training/CertDetail/'

function App() {
  return (
    <div>
      <Router>
        <Layout>
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/aboutme" exact component={AboutMe} />
            {/* <Route path="/projects" exact component={Projects} /> */}
            <Route path="/technologies" exact component={Techs} />
            <Route path="/technologies/:tech" exact component={TechDetail} />
            <Route path="/training" exact component={Training} />
            <Route path="/training/platzi" exact component={CertsPlatzi} />
            <Route path="/training/:cert" exact component={CertDetail} />
            <Route path="*" component={Error404} />
          </Switch>
        </Layout>
      </Router>
    </div>
  );
}

export default App;
