import React from 'react'
import './cert-bubble.scss'
import {Link} from 'react-router-dom'

function CertBubble(props) {
  let image = require(`../../assets/images/${props.img}`);
  return (
      <figure className="certBubble">
      <Link to={`/training/${props.uri}`}>
        <img src={image} alt={props.name} />
      </Link>
      <figcaption>
        {props.name}
      </figcaption>
    </figure>
  )
}

export default CertBubble
