// TechBubble Component

import React from 'react'
import './tech-bubble.scss'
import {Link} from 'react-router-dom'


function TechBubble (props) {
  let image = require(`../../assets/images/${props.img}`);
  return (
    <figure className="techBubble">
      <Link to={`/technologies/${props.url}`}>
        <img src={image} alt={props.name} />
      </Link>
      <figcaption>
        {props.name}
      </figcaption>
    </figure>
  )
}

export default TechBubble
