// Header Component

import React from 'react'
import {Link} from 'react-router-dom'
import headerImg from '../../../assets/images/header-img2.png'
import SocialNav from '../SocialNav'
import './header.scss'

function Header (props) {
  const handleClick = () => {
    props.toggleMenu();
  }

  return (
    <header className="header">
      <div className="header-container">
        <div className="header-content">
          <figure className="header-imgCover">
            <Link to="/">
              <img className="header-img" src={headerImg} alt="" />
            </Link>
          </figure>
          <div className="header-title">
            <h1>Alberto D.Sosa</h1>
            <small>FullStack Developer</small>
          </div>
          <div className="header-iconMenu">
            <i className="icon icon-menu" onClick={handleClick}></i>
          </div>
          <div className="header-socialnav">
            <SocialNav />
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header
